import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { ASSETS_CONFIG } from '../../../../../config/api.config';
import { SolutionsDto } from './../../../interfaces/solutions/solutions.dto';

@Injectable({
  providedIn: 'root'
})
export class SolutionsService {

  constructor(
    private readonly http: HttpClient,
  ) { }

  list(): Observable<SolutionsDto[]> {
    return this.http.get<SolutionsDto[]>(`${ASSETS_CONFIG.baseURL}/solutions.json`)
  }
}
