import { ChecklistDto } from './../../../interfaces/checklist/checklist.dto';

import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { ASSETS_CONFIG } from '../../../../../config/api.config';

@Injectable({
  providedIn: 'root'
})
export class ChecklistService {

  constructor(
    private readonly http: HttpClient,
  ) { }

  list(): Observable<ChecklistDto[]> {
    return this.http.get<ChecklistDto[]>(`${ASSETS_CONFIG.baseURL}/checklist.json`)
  }
}
