import { WhyStandDto } from './../../../interfaces/why-stand/why-stand.dto';


import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { ASSETS_CONFIG } from '../../../../../config/api.config';

@Injectable({
  providedIn: 'root'
})
export class WhyStandService {

  constructor(
    private readonly http: HttpClient,
  ) { }

  list(): Observable<WhyStandDto[]> {
    return this.http.get<WhyStandDto[]>(`${ASSETS_CONFIG.baseURL}/why-stand.json`)
  }
}
